import React from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import {ThemeProvider} from '@material-ui/core';

import Home from './views/Home';
import Mathematics from './views/Mathematics';

import MathematicsFractions from './views/Mathematics/MathematicsFractions';
import MathematicsRationals from './views/Mathematics/MathematicsRationals';

import theme from './theme/themeConfig';

function App() {

  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Switch>
          <Route exact path="/">
            <Home/>
          </Route>
          <Route exact path="/matematicas">
            <Mathematics/>
          </Route>
          <Route exact path="/matematicas/fracciones">
            <MathematicsFractions/>
          </Route>
          <Route exact path="/matematicas/enteros-decimales">
            <MathematicsRationals/>
          </Route>
          <Route path="/">
            <Home/>
          </Route>
        </Switch>
      </Router>
    </ThemeProvider>
  )
}

export default App
