import React from 'react';

import {Box, Grid} from '@material-ui/core'

export default function Operations(props){

    const {id, numerador, changeNumerador} = props;

    const handleNumerador = (e) => {
        let num = e.target.value;
        changeNumerador(num, id);
    };

    return(
        <div>    
            <Grid item>
                <Box>
                    <input type="text" className="top text-number" value={numerador[id]} onChange={handleNumerador} placeholder="Inserta Numero"/>
                </Box>
            </Grid>
        </div>
    )

}